import React, { useEffect } from 'react'
import { preLoaderAnim } from './animation';
import './preloader.css'
export default function SPLoader() {
    // const [text, setText] = useState('')
    // const [showImg, setshowImg] = useState(true)
    useEffect(() => {
        setTimeout(() => {
            preLoaderAnim()
        },3000)
    },[]);
  return (
    <>
        <div className='preloader'>
            <div className='texts-container'>
            <img style={{paddingLeft:'30px',height:'150px',width:'150px',marginLeft:'30px',marginTop:'100px'}} src='weather4.png'  alt=''/><br/>
            <h3 style={{marginTop:'100px',paddingLeft:'20px'}}>Developed by Harsh Shah</h3>
            </div>
        </div>
    </>
  )
}
