import './App.css';
import {Search,MapPin,Wind,Droplet} from 'react-feather';
import getWeather from './api/api';
import { useState } from 'react';
import dateFormat from 'dateformat'
import SPLoader from './SpinnerLoader';
function App() {
   const [city, setCity] = useState("");
   const [weather, setWeather] = useState({});

    const getWeatherbyCity= async () => {
      const weatherData = await getWeather(city);
      setWeather(weatherData);
      setCity(""); 
    } 
    const renderDate = () => {
      let now = new Date();
      return dateFormat(now,"dddd, mmmm dS, h:MM TT");
    }

    
  return (
    <>
    <SPLoader/>
    <div className='full'>
    <h1 className='harsh'>Harsh</h1>
    <div className="app">
      <h1 className='heading'>Weather app</h1>
      <div className="input-wrapper">
        <input type="text" value={city} onChange={(e) => setCity(e.target.value) } placeholder='Enter city name' />
        <button onClick={()=>getWeatherbyCity()}>
          <Search></Search>
        </button>
      </div>
      {weather && weather.weather &&
      <div className="content">
        <div className="location d-flex">
          <MapPin></MapPin>
          <h2>{weather.name} <span> ({weather.sys.country}) </span></h2>
        </div>
        <p className="datetext">{renderDate()}</p>
        <div className="weatherdesc d-flex flex-c">
          <img src={`https://openweathermap.org/img/wn/${weather.weather[0].icon}@2x.png`} alt="" />
          <h3>{weather.weather[0].description}</h3>
        </div>
        <div className="tempstats d-flex flex-c">
          <h1>{weather.main.temp} <span>&deg;C</span></h1>
          <h3>Feels Like {weather.main.feels_like} <span>&deg;C</span></h3>
        </div>
        <div className="windstats d-flex">
          <Wind></Wind>
          <h3>Wind is  {weather.wind.speed}Km/h</h3>
        </div>
        <div className="humidity d-flex">
          <Droplet></Droplet>
          <h3>humidity is  {weather.main.humidity}%</h3>
        </div>
      </div>
      
}
    {!weather.weather &&<div className="content">
        <h4>No Data Found !</h4>
      </div>}
      {/* <p>{JSON.stringify(weather)}</p> */}
    </div>
    </div>
    </>
  );
}

export default App;
